var pageContext;
export default {
	props: {
		value: {
			type: Number,
			default: 0
		},
		tabWidthMode: {
			type: String,
			default: 'equal' //equal;auto
		},
		tabs: {
			type: Array,
			default: () => {
				return [];
			}
		},
		currTabColor: {
			type: String,
			default: '#0084CD'
		},
		commonColor: {
			type: String,
			default: '#333'
		},
		tabsStyle: {
			type: Object,
			default: () => {
				return {







					borderBottom: '1rpx solid #ccc'

				};
			}
		},
		tabItemStyle: {
			type: Object,
			default: () => {
				return {
					paddingBottom: '20rpx'
				};
			}
		},
		titleStyle: {
			type: Object,
			default: () => {
				return {
					fontSize: '36rpx',
					fontWeight: 500
				};
			}
		},
		lineStyle: {
			type: Object,
			default: () => {
				return {
					width: '40rpx',
					height: '4rpx'
				};
			}
		}
	},
	data() {
		return {
			currTabIndex: 0,
			tabItemWidth: 'auto',
			tabLineWidth: 0,
			tabLineStyle: {}
		};
	},
	watch: {
		value() {
			this.currTabIndex = this.value;
			this.refreshTabList();
		}
	},
	mounted() {
		pageContext = this;
		//初始化tabs
		pageContext.initTabs();
	},
	methods: {
		getNodeInfo(nodeFlag, nodeRef, callFun) {
			uni.getSystemInfo({
				success(res) {














				}
			});
		},
		initTabs() {
			//初始化tab项宽度
			pageContext.getNodeInfo('#cigoTabs', pageContext.$refs.cigoTabs, function(tabData, res) {
				if (pageContext.tabWidthMode === 'equal') {
					let width = ((tabData.width / pageContext.tabs.length) * 2 * 375) / res.screenWidth;

					//**********************************************************
					//TODO 偶尔会出现异常，先临时延时处理，待修复
					if (width <= 0) {
						setTimeout(function() {
							//初始化tabs
							pageContext.initTabs();
						}, 500);
						return;
					}
					//**********************************************************
					pageContext.tabItemWidth = width + 'rpx';
				}
				//初始化tab横线
				pageContext.getNodeInfo('#cigo-tabs-indicator', pageContext.$refs['cigo-tabs-indicator'], function(lineData, res) {
					pageContext.tabLineWidth = lineData.width;
					pageContext.currTabIndex = pageContext.value;
					pageContext.refreshTabList();
				});
			});
		},
		refreshTabList() {
			pageContext.$nextTick(() => {
				if (pageContext.tabs.length > 0) {
					pageContext.getNodeInfo('#cigo-tabs-title-' + pageContext.currTabIndex, pageContext.$refs['cigo-tabs-title'][
						pageContext.currTabIndex
					], function(data, res) {
						let tabLineLeft = ((data.left * 2 + data.width - pageContext.tabLineWidth) * 375) / res.screenWidth;

						//**********************************************************
						//TODO 此处依然存在获取尺寸异常的情况，待修复
						console.log(tabLineLeft, data.left, data.width, pageContext.tabLineWidth);
						//**********************************************************


						pageContext.tabLineStyle = {
							transform: 'translateY(-40rpx) translateX(' + tabLineLeft + 'rpx) rotate(' + 360 * pageContext.currTabIndex +
								'deg) translateY(+40rpx)',
							transitionDuration: '500ms'
						};






					});
				}
			});
		},
		changeTab(item, index) {
			pageContext.$emit('input', index);
		}
	}
};
